<!-- yBread -->
<template>
  <div class="y-bread">
    <div
      class="nav"
      :style="{'font-size':fontSize+'px',width:realyWidth,'border-bottom':'2px solid '+borderColor}"
    >
      <section
        ref="one"
        :class="twoLevelTitle?'active':''"
        :style="{color:twoLevelTitle?borderColor:''}"
        @click="jump"
      >{{title}}</section>
      <section ref="two" v-if="twoLevelTitle">&nbsp;>&nbsp;</section>
      <section
        ref="three"
        v-if="twoLevelTitle"
        :style="{'font-size':twoFontSize+'px'}"
      >{{twoLevelTitle}}</section>
    </div>
  </div>
</template>

<script>
export default {
  name: "y-bread",
  components: {},
  props: {
    fontSize: {
      type: [Number],
      default() {
        return 22;
      }
    },
    twoFontSize: {
      type: [Number],
      default() {
        return 16;
      }
    },
    title: {
      type: [String]
    },
    twoLevelTitle: {
      type: [String],
      default() {
        return "";
      }
    },
    borderColor: {
      type: [String],
      default() {
        return "#409eff";
      }
    },
    url: {
      type: [String]
    }
  },
  data() {
    //
    return {
      realyWidth: ""
    };
  },
  // data
  computed: {},
  //data
  watch: {},
  //
  methods: {
    jump() {
      if (!this.url) {
        return;
      }
      this.$router.push(this.url);
    }
  },
  // - this
  created() {},
  // - DOM
  mounted() {
    this.$nextTick(() => {
      this.realyWidth = this.twoLevelTitle
        ? this.$refs.one.offsetWidth +
          this.$refs.two.offsetWidth +
          this.$refs.three.offsetWidth +
          65 +
          "px"
        : this.$refs.one.offsetWidth + 65 + "px";
    });
  },
  beforeCreate() {}, // - 
  beforeMount() {}, // - 
  beforeUpdate() {}, // - 
  updated() {}, // - 
  beforeDestroy() {}, // - 
  destroyed() {}, // - 
  activated() {} //keep-alive
};
</script>
<style lang='less' scoped>
//@import url(); css
.y-bread {
  width: 100%;
  height: 40px;
  border-bottom: 2px solid #d9d9d9;
  .nav {
    width: auto;
    height: 100%;
    display: flex;
    line-height: 30px;
    section:nth-child(2) {
      line-height: 30px;
    }
    .active {
      cursor: pointer;
    }
  }
}
</style>