<!-- button -->
<template>
  <el-button
    :class="backgroundColor?'y-button-'+backgroundColor:'y-button'"
    :style="{'background':backgroundColor,'border-color': backgroundColor,color:color}"
    :type="type"
    :size="size"
    :icon="icon"
    :plain="plain"
    :round="round"
    :circle="circle"
    :loading="loading"
    :disabled="disabled"
    :autofocus="autofocus"
    :native-type="nativeType"
    @click="click"
  >
    <slot></slot>
  </el-button>
</template>

<script>
export default {
  name: "y-button",
  components: {},
  props: {
    size: {
      type: [String],
      default() {
        return "small";
      }
    },
    type: {
      type: [String],
      default() {
        return "primary";
      }
    },
    icon: {
      type: [String],
      default() {
        return "";
      }
    },
    plain: {
      type: [Boolean],
      default() {
        return false;
      }
    },
    round: {
      type: [Boolean],
      default() {
        return false;
      }
    },
    circle: {
      type: [Boolean],
      default() {
        return false;
      }
    },
    loading: {
      type: [Boolean],
      default() {
        return false;
      }
    },
    disabled: {
      type: [Boolean],
      default() {
        return false;
      }
    },
    autofocus: {
      type: [Boolean],
      default() {
        return false;
      }
    },
    nativeType: {
      type: [String],
      default() {
        return "button";
      }
    },
    backgroundColor: {
      type: [String]
    },
    color: {
      type: [String],
      default() {
        return "";
      }
    }
  },
  data() {
    //
    return {};
  },
  // data
  computed: {},
  //data
  watch: {},
  //
  methods: {
    click(val){
      this.$emit('click',val)
    }
  },
  // - this
  created() {},
  // - DOM
  mounted() {},
  beforeCreate() {}, // - 
  beforeMount() {}, // - 
  beforeUpdate() {}, // - 
  updated() {}, // - 
  beforeDestroy() {}, // - 
  destroyed() {}, // - 
  activated() {} //keep-alive
};
</script>
<style lang='less' >
//@import url(); css
/**hover */
// .y-button:hover {
//   background: none !important;
//   border-color: none !important;
// }
/** */
.y-button-blue {
  background: @button-blue !important;
  border-color: @button-blue !important;
}
.y-button-blue:focus,
.y-button-blue:hover {
  background: @button-blue-hover !important;
  border-color: @button-blue-hover!important;
}
/** */
.y-button-orange {
  background: @button-orange !important;
  border-color: @button-orange!important;
}
.y-button-orange:focus,
.y-button-orange:hover {
  background: @button-orange-hover !important;
  border-color: @button-orange-hover !important;
}
/** */
.y-button-red {
  background: @button-red !important;
  border-color: @button-red !important;
}
.y-button-red:focus,
.y-button-red:hover {
  background: @button-red-hover !important;
  border-color: @button-red-hover !important;
}

/** */
.y-button-cheng {
  background: @button-cheng !important;
  border-color: @button-cheng !important;
}
.y-button-cheng:focus,
.y-button-cheng:hover {
  background: @button-cheng-hover !important;
  border-color: @button-cheng-hover !important;
}
/** */
.y-button-green {
  background: @button-green !important;
  border-color: @button-green !important;
}
.y-button-green:focus,
.y-button-green:hover {
  background: @button-green-hover !important;
  border-color: @button-green-hover !important;
}
</style>