// 
import yTable from './y-table'
import yForm from './y-form'
import yUpload from './y-upload'
import yButton from './y-button'
import yVueEditor from './y-vueEditor'
import yEditor from './y-editor'
import yBread from './y-bread'
import yPage from './y-page'
import yTree from './y-tree'
// 
const components = [
    yTable,
    yForm,
    yUpload,
    yButton,
    yVueEditor,
    yEditor,
    yBread,
    yPage,
    yTree
]
//  install  Vue  use 
const install = function (Vue) {
    // 

    if (install.installed) return
    // 
    components.map(component => Vue.component(component.name, component))
}
// 
if (typeof window !== 'undefined' && window.Vue) {
    install(window.Vue)
}
export default {
    //  install Vue.use() 
    install,
    // 
    yTable,
    yForm,
    yUpload,
    yButton,
    yVueEditor,
    yEditor,
    yBread,
    yPage,
    yTree
}
