<!-- page -->
<template>
  <div class="y-page" :style="{width:width?width+'px':'','text-align':direction}">
    <el-pagination
      background
      layout="prev, pager, next,jumper"
      :page-size="rows"
      :current-page="page"
      :total="total"
      @current-change="pageChange"
    ></el-pagination>
  </div>
</template>

<script>
export default {
  name: "y-page",
  components: {},
  data() {
    //
    return {};
  },
  props: {
    total: {
      type: [Number],
      default() {
        return 0;
      }
    },
    page: {
      type: [Number],
      default() {
        return 0;
      }
    },
    rows: {
      type: [Number],
      default() {
        return 0;
      }
    },
    width: {
      type: [Number],
      default() {
        return null;
      }
    },
    direction: {
      type: [String],
      default() {
        return "right";
      }
    }
  },
  // data
  computed: {},
  //data
  watch: {},
  //
  methods: {
    pageChange(val) {
      this.$emit("pageChange", val);
    }
  },
  // - this
  created() {},
  // - DOM
  mounted() {},
  beforeCreate() {}, // - 
  beforeMount() {}, // - 
  beforeUpdate() {}, // - 
  updated() {}, // - 
  beforeDestroy() {}, // - 
  destroyed() {}, // - 
  activated() {} //keep-alive
};
</script>
<style lang='less' scoped>
//@import url(); css
.y-page {
  width: 100%;
}
</style>