<!-- yVueEditor -->
<template>
  <div class="y-VueEditor" :style="{width:width+'px',height:(height+40)+'px'}">
    <vue-html5-editor
      :content="content"
      :height="height"
      :z-index="zIndex"
      :auto-height="autoHeight"
      @change="updateData"
    ></vue-html5-editor>
  </div>
</template>

<script>
/**vueEditor */
// 
// import Vue from "vue";
// import VueHtml5Editor from "vue-html5-editor";
export default {
  components: {},
  name: "y-vueEditor",
  props: {
    content: {
      type: String,
      default() {
        return "imgPath";
      }
    },
    width: {
      type: Number,
      default() {
        return "100%";
      }
    },
    height: {
      type: Number,
      default() {
        return "100%";
      }
    },
    zIndex: {
      type: Number,
      default() {
        return 100;
      }
    },
    autoHeight: {
      type: Boolean,
      default() {
        return true;
      }
    },
  },
  data() {
    //
    return {};
  },
  // data
  computed: {},
  //data
  watch: {},
  //
  methods: {
    updateData(e = "") {
      let c1 = e.replace(/<img width="100%"/g, "<img");
      let c2 = c1.replace(/<img/g, '<img width="100%"');
      let str = c2.replace(/crossorigin="anonymous"/g, "");
      this.$emit("updateData", str);
    }
  },
  // - this
  created() {
    
  },
  // - DOM
  mounted() {
    document.querySelector('.y-VueEditor .toolbar').style['z-index']=this.zIndex;
  },
  beforeCreate() {}, // - 
  beforeMount() {}, // - 
  beforeUpdate() {}, // - 
  updated() {}, // - 
  beforeDestroy() {}, // - 
  destroyed() {}, // - 
  activated() {} //keep-alive
};
</script>
