export function numberCheck(rule, val, callback) {
  let reg = /^[+]{0,1}(\d+)$|^[+]{0,1}(\d+\.\d+)$/
  if (!val) {
    callback()
    return false
  }
  if (!reg.test(val)) {
    callback(new Error('0'))
  } else {
    callback()
  }
}

export function telCheck(rule, val, callback) {
  let reg = /^((0\d{2,3}-\d{7,8}))$/
  if (!val) {
    callback()
    return false
  }
  if (!reg.test(val)) {
    callback(new Error(''))
  } else {
    callback()
  }
}

export function bNumberCheck(rule, val, callback) {
  let reg = /^(?!(0[0-9]{0,}$))[0-9]{1,}[.]{0,}[0-9]{0,}$/
  if (val !== 0 && !val) {
    callback()
    return false
  }
  if (!reg.test(val)) {
    callback(new Error('0'))
  } else {
    callback()
  }
}

export function numScetion1000Check(rule, val, callback) {
  let reg = /^[+]{0,1}(\d+)$|^[+]{0,1}(\d+\.\d+)$/
  if (!val) {
    callback()
    return false
  }
  if (!reg.test(val)) {
    callback(new Error('0'))
  } else if (val >= 1000) {
    callback(new Error('1000'))
  } else {
    callback()
  }
}
